/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/

/* Links */

a {
  color: $color__link;

  &:visited {
  }

  &:hover,
  &:focus,
  &:active {
    color: $color__link-hover;
  }

  &:focus {
    outline: thin dotted;
  }

  &:hover,
  &:active {
    outline: 0;
  }
}

/* Menus */

.main-navigation {
  letter-spacing: 1px;
  padding: 1rem 0;
  width: 100%;

  @include tablet {
    margin: auto;
    padding-top: 1rem;
    padding-bottom: 1.5rem;
    width: 900px;

    li.current-menu-item {
      a {
        border-bottom: $border-active;
        &:hover {
          border-bottom: $border-active;
        }
      }
    }

    @include desktop {
      width: $text-width;
    }

    a {
      // background: $primary__color;
      // color: $color__white;
      font-family: $font__main-med;
      font-size: 1rem;
      text-decoration: none;
      text-transform: uppercase;

      @include tablet {
        color: $color__text-main;
        background: $color__background-body;
        font-family: $font__secondary;
        padding: 0 0.6rem;
        padding-bottom: 2px;
      }

      @include desktop {
        font-size: 1.5rem;
      }
    }

    ul {
      // @include flex(column nowrap, center, center);
      list-style: none;
      margin: 0;
      padding-left: 0;

      @include tablet {
        @include flex(row nowrap, center, center);
        li {
          a {
            &:hover {
              border-bottom: $border-hover;
            }
          }
        }
      }
    }

    a {
      @include flex(column nowrap, center, center);
    }

    ul {
      @include flex(row nowrap, center, center);
      border-bottom: none;
      box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
      font-family: $font__main-light;
      top: $base__space-unit;
      left: -999rem;
      z-index: 99999;

      ul {
        left: -999rem;
        top: 0;
      }

      li {
        @include tablet {
          // border: $border-active;
          font-family: $font__main;
          padding: 0.5rem 0.5rem;

          &:hover a {
            background: $primary__color;
            border-bottom: transparent;
            color: $color__white;
          }
        }
      }

      a {
        @include tablet {
          color: $color__text-main;
          font-size: 1rem;
          width: 200px;
        }
      }
    }

    li:hover > ul,
    li.focus > ul {
      left: auto;
    }
  }

  li {
    padding: 0 1rem;
  }
}

/* Small menu. */

.sub-menu {
  @include tablet {
    // display: none;
    // border: 1px solid #52b0cc;

    a {
      width: 100%;
      &:hover,
      &:focus {
        background: $primary__color;
        border-bottom: transparent;
      }
    }

    li {
      padding: 0.25rem 1rem;

      &:hover {
        background: $primary__color;
        color: $color__white;
      }
    }
  }
}

.menu-toggle,
.main-navigation.toggled ul {
  display: block;
}

@media screen and (min-width: 768px) {
  .menu-toggle {
    display: none;
  }

  .main-navigation ul {
    // display: none;

    @include tablet {
      display: flex;
    }
  }
}

.comment-navigation,
.posts-navigation,
.post-navigation {
  .site-main & {
    margin: 0 0 $base__space-unit;
    overflow: hidden;
  }

  .nav-previous {
    float: left;
    width: 50%;
  }

  .nav-next {
    float: right;
    text-align: right;
    width: 50%;
  }
}
