/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #787878;
  font-family: "Quicksand", sans-serif;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.5; }

/* Headings */
h1, h2, h3, h4, h5, h6 {
  clear: both; }

/* Copy */
p {
  margin-bottom: 1.5rem; }

dfn, cite, em, i {
  font-style: italic; }

blockquote {
  margin: 0 1.5rem; }

address {
  margin: 0 0 1.5rem; }

pre {
  background: yellow;
  font-family: "Quicksand", Courier, monospace;
  font-size: 15px;
  font-size: 0.9375rem;
  line-height: 1.6;
  margin-bottom: 1.6rem;
  max-width: 100%;
  overflow: auto;
  padding: 1.6rem; }

code, kbd, tt, var {
  font-family: "Muli-Bold", "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: 15px;
  font-size: 0.9375rem; }

abbr, acronym {
  border-bottom: 1px dotted 1px dotted black;
  cursor: help; }

mark, ins {
  background: purple;
  text-decoration: none; }

big {
  font-size: 125%; }

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
  font-size: 16px;
  font-size: 1rem; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background: #f0f3f8;
  /* Fallback for when there is no custom background color defined. */ }

blockquote,
q {
  quotes: '' ''; }
  blockquote:before, blockquote:after,
  q:before,
  q:after {
    content: ''; }

hr {
  background-color: green;
  border: 0;
  height: 1px;
  margin-bottom: 1.5rem; }

ul,
ol {
  margin: 0 0 1.5rem 3rem; }

ul {
  list-style: disc; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5rem; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5rem 1.5rem; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

table {
  margin: 0 0 1.5rem;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
.button-purchase, .cfs-hyperlink {
  background: #b4de96;
  border-color: #b4de96;
  border-radius: 25px;
  color: #fff;
  text-transform: uppercase;
  font-family: "Muli", sans-serif;
  padding: 0.8rem 6rem;
  font-size: 17px;
  font-weight: bold;
  list-style: none;
  text-decoration: none; }
  .button-purchase:hover, .cfs-hyperlink:hover {
    background: #1c88a8; }

.button {
  background: #b4de96;
  border-color: #b4de96;
  border-radius: 25px;
  color: #fff;
  text-transform: uppercase;
  font-family: "Muli", sans-serif;
  padding: 0.8rem 6rem;
  font-size: 17px;
  font-weight: bold;
  list-style: none;
  text-decoration: none; }
  .button:hover {
    background: #1c88a8; }

/* Fields */
input[type='text'],
input[type='email'],
input[type='url'],
input[type='password'],
input[type='search'],
textarea {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 3px; }
  input[type='text']:focus,
  input[type='email']:focus,
  input[type='url']:focus,
  input[type='password']:focus,
  input[type='search']:focus,
  textarea:focus {
    color: #111; }

input[type='text'],
input[type='email'],
input[type='url'],
input[type='password'],
input[type='search'] {
  padding: 3px; }

textarea {
  padding-left: 3px;
  width: 100%; }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
a {
  color: #9ec284; }
  a:hover, a:focus, a:active {
    color: #1c88a8; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/* Menus */
.main-navigation {
  letter-spacing: 1px;
  padding: 1rem 0;
  width: 100%; }
  @media (min-width: 500px) {
    .main-navigation {
      margin: auto;
      padding-top: 1rem;
      padding-bottom: 1.5rem;
      width: 900px; }
      .main-navigation li.current-menu-item a {
        border-bottom: 2px solid #52b0cc; }
        .main-navigation li.current-menu-item a:hover {
          border-bottom: 2px solid #52b0cc; } }
  @media (min-width: 500px) and (min-width: 1024px) {
    .main-navigation {
      width: 1040px; } }
  @media (min-width: 500px) {
      .main-navigation a {
        font-family: "Quicksand-Medium", sans-serif;
        font-size: 1rem;
        text-decoration: none;
        text-transform: uppercase; } }
    @media (min-width: 500px) and (min-width: 500px) {
      .main-navigation a {
        color: #787878;
        background: #f0f3f8;
        font-family: "Muli", sans-serif;
        padding: 0 0.6rem;
        padding-bottom: 2px; } }
    @media (min-width: 500px) and (min-width: 1024px) {
      .main-navigation a {
        font-size: 1.5rem; } }
  @media (min-width: 500px) {
      .main-navigation ul {
        list-style: none;
        margin: 0;
        padding-left: 0; } }
    @media (min-width: 500px) and (min-width: 500px) {
      .main-navigation ul {
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center; }
        .main-navigation ul li a:hover {
          border-bottom: 2px solid #b5ccdb; } }
  @media (min-width: 500px) {
      .main-navigation a {
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center; }
      .main-navigation ul {
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;
        border-bottom: none;
        box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
        font-family: "Quicksand-Light", sans-serif;
        top: 1.5rem;
        left: -999rem;
        z-index: 99999; }
        .main-navigation ul ul {
          left: -999rem;
          top: 0; } }
    @media (min-width: 500px) and (min-width: 500px) {
      .main-navigation ul li {
        font-family: "Quicksand", sans-serif;
        padding: 0.5rem 0.5rem; }
        .main-navigation ul li:hover a {
          background: #52b0cc;
          border-bottom: transparent;
          color: #fff; } }
    @media (min-width: 500px) and (min-width: 500px) {
      .main-navigation ul a {
        color: #787878;
        font-size: 1rem;
        width: 200px; } }
  @media (min-width: 500px) {
      .main-navigation li:hover > ul,
      .main-navigation li.focus > ul {
        left: auto; } }
  .main-navigation li {
    padding: 0 1rem; }

/* Small menu. */
@media (min-width: 500px) {
  .sub-menu a {
    width: 100%; }
    .sub-menu a:hover, .sub-menu a:focus {
      background: #52b0cc;
      border-bottom: transparent; }
  .sub-menu li {
    padding: 0.25rem 1rem; }
    .sub-menu li:hover {
      background: #52b0cc;
      color: #fff; } }

.menu-toggle,
.main-navigation.toggled ul {
  display: block; }

@media screen and (min-width: 768px) {
  .menu-toggle {
    display: none; } }

@media screen and (min-width: 768px) and (min-width: 500px) {
  .main-navigation ul {
    display: flex; } }

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5rem;
  overflow: hidden; }

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: left;
  width: 50%; }

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  float: right;
  text-align: right;
  width: 50%; }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: red;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: royalblue;
    display: block;
    font-size: 14px;
    font-size: 0.875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
  display: table; }

.widget {
  margin: 0 0 1.5rem;
  /* Make sure select elements fit in widgets. */ }
  .widget select {
    max-width: 100%; }

/* Search widget. */
.widget_search .search-submit {
  display: none; }

/* Footer */
.site-footer {
  background-color: #52b0cc;
  display: flex;
  flex-flow: column;
  align-items: center;
